body {
  margin: 0;
  font-family: 'Nanum Gothic' ;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  position: fixed;
  text-align: center;
  width: 100%;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.contents {
  display: -webkit-flex;
  display: flex;
  height: 100%;
  position: relative;
  overflow: hidden;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.contents .mode-change-button {
  width: 40px;
  height: 40px;
  position: absolute;
  top: 40px;
  left: calc(75% - 40px);
  z-index: 600;
  background-color: white;
  border: none;
  border-radius: 0.25rem 0 0 0.25rem;
  box-shadow: 0 0 2px 2px #8080806b;
  -webkit-clip-path: inset(-2px 0px -2px -2px);
          clip-path: inset(-2px 0px -2px -2px);
  color: #8080806b;
  cursor: pointer;
}

.map-container {
  width: 75%;
  height: 100%;
}

.leaflet-container {
  width: 100%;
  height: 100vh;
  background-color: black;
}

.leaflet-control-container {
  display: none;
}

.alert-box {
  margin: 10px;
  padding: 10px;
  border-radius: 10px;
  background-color: #ffffffaa;
  position: absolute;
  bottom: 0;
  right: 25%;
  z-index: 500;
  width: 250px;
  height: 100px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.leaflet-container span {
  font-weight: bold;
}

.busIcon {
  width: 20px;
  height: 20px;
  padding: 2px;
}

.leaflet-tooltip-top::before {
  margin-bottom: -6px !important;
  border-top-color: #1478ff !important;
}

.leaflet-tooltip {
  background-color: #00000000 !important;
  border-style: none !important;
  box-shadow: none !important;
}

.leaflet-tooltip>div {
  background-color: white;
  border-color: #1478ff;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  border-radius: 50px;
  padding: 5px;
  border-width: 2px;
  border: 3px solid #1478ff;
  box-shadow: 0 1px 3px rgb(0 0 0 / 40%);
}

.leaflet-tooltip>div.select {
  background-color: #1478ff !important;
  border-style: none !important;
}

.leaflet-tooltip>div div:first-child {
  padding: 7px;
  border-radius: 100%;
  display: -webkit-flex;
  display: flex;
  background-color: #1478ff;
}

.leaflet-tooltip>div.select div:first-child {
  background-color: white;
}

.leaflet-tooltip>div div:nth-child(2) {
  margin-left: 7px;
  margin-right: 7px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  color: black;
}

.leaflet-tooltip>div.select div:nth-child(2) {
  color: white;
}

.leaflet-tooltip>div div span {
  text-align: left;
}

.leaflet-tooltip>div div span:first-child {
  font-size: 0.85rem;
  font-weight: bold;
}

.leaflet-tooltip>div div span:last-child {
  font-size: 0.6rem;
}

/* Popup.js */
.modal-container {
  z-index: 500;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background: rgba(0, 0, 0, 0.6);
}

.modal {
  width: 60%;
  height: 90%;
  background-color: white;
  box-sizing: border-box;
  background: #fff;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  border-radius: 20px;
}

.modal button {
  width: 30px;
  height: 30px;
  -webkit-align-self: flex-end;
          align-self: flex-end;
  margin: 20px;
  font-size: 20px;
  font-weight: bold;
  background-color: transparent;
  color: black;
  border: none;
  cursor: pointer;
}

.modal .info-content {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  overflow: auto;
  margin: 0 2rem 2rem 2rem;
}

.modal .info-content .station-name {
  font-weight: bold;
  margin: 0;
  margin-bottom: 20px;
  font-size: 1.5em;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  position: relative;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.modal .info-content .station-name .node-id {
  font-size: 1rem;
}

.modal .info-content .station-name .refresh {
  margin-left: 1rem;
  font-size: 1rem;
  cursor: pointer;
  position: absolute;
  right: -2rem;
}

.modal .info-content .station-name font {
  padding: 5px 15px;
  background-color: antiquewhite;
}

.info img {
  margin: 10px;
}

.modal .info-content .route-number {
  font-weight: bold;
  vertical-align: middle;
  width: 30%;
}

.arrival-info-container {
  width: 100%;
  height: 100%;
  overflow: auto;
}

.modal .info-content .arrival-info-container>div:first-child {
  border-top: 1px solid black;
}

.modal .info-content .arrival-info-container>div {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  padding: 15px;
  border-bottom: 1px solid black;
  cursor: pointer;
}

.arrival-bus-container {
  width: 70%;
}

.arrival-bus-container div {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  padding-bottom: 5px;
  padding-top: 5px;
}

.arrival-bus-container div span {
  width: 50%;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  width: 100%;
  z-index: 500;
  background-color: #464646;
  min-height: 60px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  box-shadow: 0 4px 2px -2px gray;
  color: white;
  font-weight: bold;
}

.App-header .logo {
  margin-left: 1rem;
}

.App-link {
  color: #61dafb;
}

/* 모바일 */
@media (max-width: 767px) {
  .alert-box {
    right: 0;
    top: 60px;
  }

  .contents {
    -webkit-flex-direction: column-reverse;
            flex-direction: column-reverse;
  }

  .map-container {
    width: 100%;
    height: 100%;
  }

  .modal {
    width: 80%;
    height: 50%;
    position: relative;
  }

  .modal>button {
    position: absolute;
    top: 0;
    right: 0;
  }

  .modal .info-content {
    margin-top: 2rem;
  }

  .modal .info-content .station-name {
    font-size: 1rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .modal .info-content .station-name .node-id {
    font-size: 0.85rem;
  }

  .modal .info-content .station-name .refresh {
    font-size: 0.85rem;
    margin-left: 1rem;
  }

  .modal .info-content .route-number {
    font-size: 0.85rem;
  }


  .arrival-bus-container div span {
    font-size: 0.75rem;
  }

}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
